<template>
	<view>
		<!-- 出售商品页面 -->
		<view class="releaseSellsHead">

			<!-- 商品基本信息 -->
			<view class="rSellsBase">
				<view class="rSellsBase-title">商品</view>
				<view class="rSellsBase-img"><img class="rSellsBase-pic" src="" alt="" @click="GetSellsImg"></view>
				<view class="rSellsBase-info">

					<view class="sellsBase-name" @click="GetSellsName">
						{{releaseSellInfo.name}}
					</view>
					<view class="sellsBase-class" @click="GetSellsClass">
						{{releaseSellInfo.class2}}-{{releaseSellInfo.class3}}
					</view>
					<view class="sellsBase-has" @click="GetSellsOwnNum">
						我拥有{{releaseSellInfo.ownNumber}}个
					</view>
				</view>
			</view>

			<view class="rSellsBase">
				<view class="rSellsBase-title" @click="test">价格</view>
				<input v-model="releaseSellInfo.sellPrice" class="rSellsBase-input" type="digit" @blur="blur"
					placeholder="请输入金额" />
			</view>
			<view class="rSellsTips">成交均价xxx.00元</view>
			<view class="rSellsBase">
				<view class="rSellsBase-title">数量</view>
				<view class="goodsNum">
					<view class="btn left" @click="minus">-</view>
					<view class="num">{{releaseSellInfo.sellCount}}</view>
					<view class="btn right" @click="add">+</view>
				</view>
			</view>
			<view class="rSellsTips">最多出售1个</view>
		</view>
		<!-- 出售规则 -->
		<view class="reRules ">
			<!-- 规则标题 -->
			<view class="sellRule">
				<view class="logo">?</view>
				<view>
					出售规则
				</view>
			</view>
			<!-- 规则详情 -->
			<view class="text">
				<view>
					1.发布闪购商品，定价后系统会根据所有玩家出价由低到高显示，买价自动购买最低价商品
				</view>
				<view>
					2.当出售价低于已有挂单的售价，将会以售价和对方直接成交。
				</view>
			</view>
		</view>

		<!-- 确定发布按钮 -->
		<view class="releaseSellsButtom">
			<button class="confirm" plain="true" @click="Confirm">确认</button>
		</view>
	</view>

</template>

<script>
	export default {
		data() {
			return {
				releaseSellInfo: {
					// imgurl:,
					name: "kimi", //名称
					class2: 'ki',
					class3: 2022,
					ownNumber: 1, // 拥有数量
					sellPrice: '', // 售价
					sellCount: 1 // 售出数量
				}

			};
		},
		onLoad() {

		},


		methods: {
			test() {
				console.log(this.releaseSellInfo)
			},

			// 价格输入框正则判断
			blur(e) {
				this.releaseSellInfo.sellPrice = Math.floor(e.detail.value * 100) / 100;
				if (this.releaseSellInfo.sellPrice <= 0) {
					this.releaseSellInfo.sellPrice = 0;
				}
			},
			// 获取图片
			GetSellsImg() {

			},
			// 获取名称
			GetSellsName() {

			},
			// 获取类目
			GetSellsClass() {

			},
			GetSellsOwnNum() {

			},
			// 按钮减
			minus() {

				if (this.releaseSellInfo.sellCount <= 1) {
					this.releaseSellInfo.sellCount == 1;
				} else {
					this.releaseSellInfo.sellCount--;

				}
			},
			// 按钮加
			add() {
				if (this.releaseSellInfo.sellCount >= 100) {
					this.releaseSellInfo.sellCount == 100;
				} else {
					this.releaseSellInfo.sellCount++;
				}
			},
			// 确认提交
			Confirm() {

			}
		},
		onShow() {
			// this.getReleaseSellsForm()
		}
	}
</script>

<style lang="scss">
	.releaseSellsHead {
		.rSellsBase {
			margin: 30rpx 0 30rpx 30rpx;
			display: flex;
			flex-wrap: nowrap;
			align-items: center;
			justify-content: flex-start;

			.rSellsBase-title {
				font-size: 26rpx;
			}

			.rSellsBase-img {
				margin-left: 45rpx;
				width: 120rpx;
				height: 120rpx;
				border-radius: 10rpx;
				background-color: #d8d6d6;

			}

			.rSellsBase-info {
				margin-left: 23rpx;
				height: 120rpx;

				.sellsBase-name {
					height: 27rpx;
					font-size: 30rpx;
				}

				.sellsBase-class {
					height: 23rpx;
					margin-top: 12rpx;
					font-size: 24rpx;
					color: #807f7f;
				}

				.sellsBase-has {
					height: 23rpx;
					margin-top: 28rpx;
					margin-bottom: 8rpx;
					font-size: 24rpx;
					color: #807f7f;
				}
			}

			.rSellsBase-input {
				font-size: 24rpx;
				margin-left: 48rpx;
				padding-left: 16rpx;
				background-color: #fdf3de;
				width: 592rpx;
				height: 88rpx;
				border-radius: 10rpx;
			}


			.goodsNum {
				display: flex;
				margin-left: 50rpx;

				.btn {
					width: 80rpx;
					height: 48rpx;
					background-color: #fdfcfc;
					text-align: center;
					border: 1rpx solid #ccc;
				}

				.num {
					width: 50rpx;
					border-top: 1rpx solid #ccc;
					border-bottom: 1rpx solid #ccc;
					background-color: #f7f5f5;
					text-align: center;
				}

				.left {
					width: 50rpx;
					border-top-left-radius: 10rpx;
					border-bottom-left-radius: 10rpx;
				}

				.right {
					width: 50rpx;
					border-top-right-radius: 10rpx;
					border-bottom-right-radius: 10rpx;
				}
			}

		}

		.rSellsTips {

			margin-left: 140rpx;
			font-size: 28rpx;
			color: #646363;
		}
	}

	.reRules {
		margin-left: 30rpx;
		margin-top: 60rpx;
		color: #5e5e5e;
		font-size: 26rpx;

		.sellRule {
			width: 100%;
			display: flex;
			justify-content: flex-start;

			.logo {
				width: 24px;
				height: 24px;
				border-radius: 50%;
				text-align: center;
				line-height: 40rpx;
				border: 1rpx solid #5E5E5E;
			}

		}

		.text {
			margin: 24rpx 15rpx;
			font-size: 26rpx;
		}
	}


	.releaseSellsButtom {
		width: 100%;
		display: flex;
		justify-content: center;

		.confirm {
			background-color: #FB9503;
			margin: 0 auto;
			color: #fff;
			border-radius: 34rpx;
			border-color: #FB9503;
			width: 340rpx;
			height: 68rpx;
			position: fixed;
			bottom: 9rpx;
			line-height: 68rpx;
		}
	}
</style>
